button.button,
a.button {
  display: inline-block;
  text-decoration: none;
  padding: 8px 30px;
  border-radius: 3px;
  text-align: center;
  position: relative;
  cursor: pointer;
  background: transparent;
  color: #fff;
  border: 1px solid #fff;
}
button.button.disabled,
a.button.disabled {
  opacity: 0.6;
  pointer-events: none;
}
button.button.loading,
a.button.loading {
  color: transparent !important;
  text-shadow: none !important;
  pointer-events: none;
}
button.button.loading:after,
a.button.loading:after {
  -webkit-animation: spin-around 400ms infinite linear;
          animation: spin-around 400ms infinite linear;
  border: 2px solid #fff;
  border-radius: 50%;
  border-right-color: transparent;
  border-top-color: transparent;
  content: "";
  display: block;
  height: 16px;
  width: 16px;
  left: 50%;
  margin-left: -10px;
  margin-top: -10px;
  top: 50%;
  position: absolute;
}
button.button:focus,
a.button:focus {
  outline: 0;
}
button.button.accent,
a.button.accent {
  border: 1px solid #d93400;
  text-shadow: 1px 1px 1px #e73700;
  background: -webkit-linear-gradient(top, #ff5722 0%, #f63b00 100%);
  background: linear-gradient(to bottom, #ff5722 0%, #f63b00 100%);
  color: #fff;
}
button.button.accent:hover,
a.button.accent:hover {
  background: -webkit-linear-gradient(top, #ff7043 0%, #f63b00 100%);
  background: linear-gradient(to bottom, #ff7043 0%, #f63b00 100%);
}
button.button.primary,
a.button.primary {
  border: 1px solid #485e68;
  text-shadow: 1px 1px 1px #4d646f;
  background: -webkit-linear-gradient(top, #607d8b 0%, #526a76 100%);
  background: linear-gradient(to bottom, #607d8b 0%, #526a76 100%);
  color: #fff;
}
button.button.primary:hover,
a.button.primary:hover {
  background: -webkit-linear-gradient(top, #7592a0 0%, #526a76 100%);
  background: linear-gradient(to bottom, #7592a0 0%, #526a76 100%);
}
button.button.secondary,
a.button.secondary {
  border: 1px solid #a7b5be;
  text-shadow: 1px 1px 1px #b5c1c9;
  background: -webkit-linear-gradient(top, #eceff1 0%, #c3ccd3 100%);
  background: linear-gradient(to bottom, #eceff1 0%, #c3ccd3 100%);
  color: rgba(0,0,0,0.87);
}
button.button.secondary:hover,
a.button.secondary:hover {
  background: -webkit-linear-gradient(top, #eff1f3 0%, #c3ccd3 100%);
  background: linear-gradient(to bottom, #eff1f3 0%, #c3ccd3 100%);
}
button.button.secondary.loading:after,
a.button.secondary.loading:after {
  border-left-color: #000;
  border-bottom-color: #000;
}
.fourth-container {
  width: calc(99.99% * 1/4 - (61px - 61px * 1/4));
}
.fourth-container:nth-child(1n) {
  float: left;
  margin-right: 61px;
  clear: none;
}
.fourth-container:last-child {
  margin-right: 0;
}
.fourth-container:nth-child(4n) {
  margin-right: 0;
  float: right;
}
.fourth-container:nth-child(4n + 1) {
  clear: left;
}
.fourth-container .color-container {
  height: 100px;
}
.fourth-container .color-container.primary {
  background: #607d8b;
}
.fourth-container .color-container.primary-light {
  background: #eceff1;
}
.fourth-container .color-container.accent {
  background: #ff5722;
}
.fourth-container .color-container.accent-green {
  background: #009245;
}
.fourth-container p {
  text-align: center;
}
.logo-container {
